"use strict";
exports.kaartHtml = "\n<div id=\"kaart\">\n\n  <div id=\"zoombuttons\">\n    <p>Zoom: alt + muiswiel<br>\n      Pan: alt + slepen\n    </p>\n    <a id=\"startpositie\" (click)=\"startpositie()\" class=\"disabled\">Terug naar<br>startpositie</a> \n  </div>\n  <div id=\"legenda\">\n  <div class=\"legenda-item\" *ngFor=\"let value of ['doet mee', 'heeft belangstelling', 'doet niet mee']\">\n      <div class=\"legenda-blokje\" [ngClass]=setDmClasses2(value)></div>\n      <span>{{value}}</span>\n    </div>\n    <div class=\"aantal-inwoners\">\n    <table>\n    <tr><td colspan=\"2\">Aantal inwoners</td></tr>\n    <tr><td>rhe:</td><td>minder dan 29.500</td></tr>\n    <tr><td>Wag:</td><td>tussen 29.500 en 79.500</td></tr>\n    <tr><td>ARN:</td><td>meer dan 79.500</td></tr>\n    </table>\n    </div>\n  </div>\n\n  <svg viewBox=\"0 0 640 687\">\n    <rect id=\"pan-rect\" width=\"600\" height=\"687\"></rect>\n    <g id=\"container\">\n      <g id=\"pathes\">\n        <path *ngFor=\"let path of svgPathes\" [attr.d]=\"path.path\" (mousedown)=\"toonForm(path.GM_CODE)\" (mouseover)=\"popupInfo([path.centroid, path.GM_CODE, path.AANT_INW])\" (mouseout)=\"hidePopupInfo()\" [ngClass]=\"setDmClasses1(path.GM_CODE)\"></path>\n      </g>\n      <g id=\"labels\">\n        <text class=\"gem-naam\" *ngFor=\"let label of svgLabels\" [attr.x]=\"label.x\" [attr.y]=\"label.y\" [style.font-size]=\"fontSize\" [ngClass]=setDmClasses1(label.GM_CODE)>{{label.text}}</text>\n      </g>\n      <g id=\"popupInfo\"*ngIf=\"showPopup&&!showForm\">\n        <rect  [attr.x]=\"popupData.x\" [attr.y]=\"popupData.y\" [attr.width]=\"popupWidth\" height=\"25\"></rect>\n        <text id=\"popupInfoText\"  [attr.x]=\"popupData.x\" [attr.y]=\"popupData.y\">\n          <tspan [attr.x]=\"popupData.x + 3\" dy=\"10\">{{popupData.GM_NAAM}}:</tspan>\n          <tspan [attr.x]=\"popupData.x + 3\" dy=\"10\">{{popupData.INW}}.000 inwoners</tspan>\n        </text>\n      </g>\n    </g>\n  </svg>\n\n  <div *ngIf=\"showForm\" id=\"dialog\">  \n    <h3>{{formGemNaam}}</h3>\n    <form>\n      <div id=\"radio\">\n        <div *ngFor=\"let value of ['ja', 'belangstelling', 'nee']\" [ngClass]=\"setDmClasses2(value)\" [class.checked]=\"formGemDM==value\">\n          <label>\n          <input  type=\"radio\" name=\"radio\" (click)=\"formGemDM=value\" [checked]=\"formGemDM==value\" value={{value}}>\n          {{value}}</label>\n        </div>\n      </div>\n      <div id=\"formButtons\">\n        <a (click)=\"nietOpslaan()\">Sluiten</a>\n        <a [class.disabled]=\"currentGemDM==formGemDM\" (click)=\"opslaan(formGemCode)\">Opslaan<br>en sluiten</a>\n      </div>\n    </form>\n  </div>\n</div>\n";
//# sourceMappingURL=kaart.html.js.map


